<script>
  import Section from "./Section.svelte";
  import { blog } from "../Store/global-store.js";

  let blogsItems = [];
  let unsubscribe = blog.subscribe(blogs => {
    blogsItems = [...blogs];
  });
  function formatDate(date) {
    console.log(new Date(date));
    let day = new Date(date).getDate();
    let month = new Date(date).getMonth();
    let year = new Date(date).getFullYear();

    return `${day} / ${month} / ${year}`;
  }
</script>

<style>
  .image > img {
    width: 50px;
  }
</style>

<Section>
  <div slot="header">
    <h1>My Blog</h1>
  </div>
  <div class="post">
    {#each blogsItems as blog (blog.id)}
      <div class="image">
        <img src={blog.logo[0]} alt="" />
      </div>
      <div class="post-title">
        <div class="title">
          <h1>{blog.title}</h1>
        </div>
        <div class="subtitle">
          <h3>Posted by Artur</h3>
          <p>{formatDate(blog.date)}</p>
        </div>
      </div>
      <div class="post-body">
        <p>
          {@html blog.description}
        </p>
      </div>
    {/each}
  </div>

</Section>
